<template>
  <div id="fireControl">
    <div id="myheader">
      <div class="container-fluid" style="padding:0px;height:100%;width:100%;">
        <div class="row" style="margin:0px;height:100%;width:100%;">
          <div class="col-xs-2" style="padding:0px;height:100%;"><img src="../assets/logo.png" alt="logo" height="90%"></div>
          <div class="col-xs-8" style="padding:0px;height:100%;"><div class="text-center" id="text">智慧消防指挥平台</div></div>
        </div>
      </div>
    </div>
    <div id="main">
      <div id="menu">
        <div id="icons">
            <input class="icon" type="checkbox" v-model="firemenStatus"><img src="../assets/fireman.png" alt="firemen"><span>消防员</span>
            <input class="icon" type="checkbox" v-model="carStatus"><img src="../assets/car.png" alt="car"><span>消防车</span>
            <input class="icon" type="checkbox" v-model="hydrantStatus"><img src="../assets/hydrant.png" alt="hydrant"><span>消防栓</span>
            <input class="icon" type="checkbox" v-model="smokeStatus"><img src="../assets/smoke.png" alt="smoke"><span>烟雾报警器</span>
            <input class="icon" type="checkbox" v-model="wsdStatus"><img src="../assets/wsd2.png" alt="wsd"><span>电气火灾监控设备</span>
            <input class="icon" type="checkbox" v-model="stationStatus"><img src="../assets/station.png" alt="station"><span>一级消防站</span>     
            <input class="icon" type="checkbox" v-model="miniStatus"><img src="../assets/mini.png" alt="mini"><span>微型消防站</span>  
            <input class="icon" type="checkbox" v-model="roomStatus"><img src="../assets/room.png" alt="room"><span>消防控制室</span> 
            <span id="data" @click="showData()"><Icon type="ios-paper" size="20" /><span style="margin:0px 3px;">数据统计</span> </span>
        </div>     
      </div>
      <div id="map">
        <div id="mapshow"></div>
      </div>
    </div>
    <!-- 消防员弹窗 -->
    <div class="videoShow" id="firemen" v-if="firemenVideo">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="firemenVideo=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="firemenMsg"></div>
    </div>
    <!-- 消防车弹窗 -->
    <div class="videoShow" id="car" v-if="carVideo">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="carVideo=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="carMsg"></div>
    </div>
    <!-- 一级消防站、微型消防站、消防控制室弹窗 -->
    <div class="videoShow" id="station" v-if="stationVideo">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="stationVideo=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="stationMsg"></div>
    </div>
    <!-- 消防栓、烟雾感应器弹窗 -->
    <div class="videoShow" id="picture" v-if="picture">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="picture=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="pictureMsg"></div>
    </div>
    <!-- 消防汇总 -->
    <div id="allData" v-if="dataShow">
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">2人</div><div>消防人员</div></div>
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">2辆</div><div>消防车</div></div>
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">1处</div><div>消防栓</div></div>
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">4个</div><div>消防站</div></div>
      <div class="fireCell" style="border:none;"><div style="color:rgb(2, 252, 196);">2个</div><div>消防控制室</div></div>
    </div>
    <!-- 数据统计 -->
    <div class="title1" v-if="dataShow">数据统计</div>
    <div id="dataWindow" v-if="dataShow">
      <div class="row">
        <div class="col-xs-6 text-right">今日成灾火警：</div>
        <div class="col-xs-6 text-left" style="color:#d9534f;">2起</div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right">今日告警：</div>
        <div class="col-xs-6 text-left" style="color:#02fcc4;">3起</div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right">今日隐患数：</div>
        <div class="col-xs-6 text-left" style="color:#02fcc4;">85起</div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right">消防设施完好率：</div>
        <div class="col-xs-6 text-left">
          <div class="progress">
            <div class="progress-bar" role="progressbar" aria-valuenow="93" aria-valuemin="0" aria-valuemax="100" style="width: 93%;">
              93%
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right">今日值班人数在岗率：</div>
        <div class="col-xs-6 text-left">
          <div class="progress">
            <div class="progress-bar progress-bar-warning" role="progressbar" aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style="width: 100%;">
              100%
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right">隐患整改率：</div>
        <div class="col-xs-6 text-left">
          <div class="progress">
            <div class="progress-bar progress-bar-danger" role="progressbar" aria-valuenow="99" aria-valuemin="0" aria-valuemax="100" style="width: 99%;">
              99%
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 今日报警 -->
    <div class="title2" v-if="dataShow">历史报警</div>
    <div id="alarm" v-if="dataShow">
      <!-- <div id="tableHead"><div class="cell">所属单位</div><div class="cell">设备名称</div><div class="cell">时间</div><div class="cell">类型</div><div class="cell">结果</div><div class="cell">操作</div></div>
      <div id="tableBody">
        <div class="cells" v-for="msg in message" :key="msg.id"><div class="cell">{{msg.unit}}</div><div class="cell">{{msg.name}}</div><div class="cell">{{msg.time}}</div><div class="cell">{{msg.type}}</div><div class="cell">{{msg.result}}</div><div class="cell"><Icon type="md-eye" size="18" @click="showAlarm(msg.src)" /><Icon type="md-videocam" size="18" @click="showAlarm(msg.src)" /></div></div>
      </div> -->
      <div id="tableHead"><div class="cell">设备类型</div><div class="cell">区域名称</div><div class="cell">类型</div><div class="cell">时间</div><div class="cell">结果</div><div class="cell">操作</div></div>
      <div id="tableBody">
        <div class="cells" v-for="msg in alarmMessage" :key="msg.id"><div class="cell">{{msg.deviceType}}</div><div class="cell">{{msg.areaName}}</div><div class="cell">{{msg.alarmBizName}}</div><div class="cell">{{msg.startTime}}</div><div class="cell">{{msg.handle_status}}</div><div class="cell"><Icon type="md-eye" size="18" /><Icon type="md-videocam" size="18" /></div></div>
      </div>
    </div>
    <!-- 今日报警视图窗口 -->
    <div class="videoShow" id="alarmPicture" v-if="alarmStatus">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="alarmStatus=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="alarmMsg"></div>
    </div>
    <!-- 告警提示窗口 -->
    <div class="warn">
      <Alert type="warning" show-icon v-for="msg in alarms" :key="msg.id">
        警告&#160;&#160;&#160;&#160;&#160;&#160;&#160;&#160;{{msg.startTime}}  <Icon type="md-close" @click="deleteThis(msg.id)" style="float:right"/>
        <template slot="desc">
          ID为{{msg.deviceID}}的{{msg.deviceType}}设备，发出 <span style="color:red">{{msg.alarmBizName}}</span> 
        </template>
      </Alert>
    </div>
  </div>
</template>

<script>
import {styleJson} from '../js/mapstyle'
export default {
  name: 'fireControl',
  data () {
    return {
      //地图实例
      map:null,
      //消防员
      firemenStatus:false,
      firemen:[
        // {name:"陈国忠",deviceNum:"LGW1_001",num:"0110",img:require("../assets/firemen/2.png"),lng:113.41104,lat:22.814871,rtsp:"rtsp://238o86j398.qicp.vip:32943/live/av0",video:require("../assets/videos/1.mp4")},
        // {name:"李超",deviceNum:"LGW1_002",num:"0111",img:require("../assets/firemen/3.png"),lng:113.381647,lat:22.796215,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
        {name:"陈国忠",deviceNum:"LGW1_001",num:"0110",img:require("../assets/firemen/2.png"),lng:113.484324,lat:23.187536,rtsp:"rtsp://238o86j398.qicp.vip:32943/live/av0",video:require("../assets/videos/1.mp4")},
        {name:"李超",deviceNum:"LGW1_002",num:"0111",img:require("../assets/firemen/3.png"),lng:113.494744,lat:23.173051,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
      ],     
      firemenMarkers:[],
      firemenVideo:false,
      firemenMsg:"",
      //消防车
      carStatus:false,
      cars:[
        // {licenseNum:"粤A5263",deviceNum:"LGW2_001",num:"0210",img:require("../assets/cars/car1.png"),lng:113.41801,lat:22.793083,rtsp:"rtsp://238o86j398.qicp.vip:32943/live/av0",video:require("../assets/videos/1.mp4")},
        // {licenseNum:"粤A8456",deviceNum:"LGW2_002",num:"0211",img:require("../assets/cars/car1.png"),lng:113.389911,lat:22.821933,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
        {licenseNum:"粤A5263",deviceNum:"LGW2_001",num:"0210",img:require("../assets/cars/car1.png"),lng:113.529598,lat:23.188665,rtsp:"rtsp://238o86j398.qicp.vip:32943/live/av0",video:require("../assets/videos/1.mp4")},
        {licenseNum:"粤A8456",deviceNum:"LGW2_002",num:"0211",img:require("../assets/cars/car1.png"),lng:113.502074,lat:23.194047,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
      ],
      carMarkers:[],
      carVideo:false,
      carMsg:"",
      //消防栓
      hydrantStatus:false,
      // hydrants:[
      //   {lng:113.390007,lat:22.806769,img:require("../assets/hydrants/hydrant1.png")},
      //   {lng:113.388489,lat:22.80846,img:require("../assets/hydrants/hydrant2.png")}
      // ],
      hydrants:[],
      hydrantsMarkers:[],
      picture:false,
      pictureMsg:"",
      //烟雾报警器
      smokeStatus:false,
      // smokes:[
      //   {lng:113.372282,lat:22.821729,img:require("../assets/smokes/smoke1.png")},
      //   {lng:113.435163,lat:22.823727,img:require("../assets/smokes/smoke2.png")}
      // ],
      smokes:[],
      smokeMarkers:[],
      //电气火灾监控设备
      wsdStatus:false,
      wsd:[],
      wsdMarkers:[],
      //一级消防站
      stationStatus:false,
      stations:[
        // {name:"豪岗社区一级消防站",deviceNum:"LGW3_001",num:"0310",lng:113.434445,lat:22.79721,img:require("../assets/stations/station1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        // {name:"潭州社区一级消防站",deviceNum:"LGW3_002",num:"0311",lng:113.407731,lat:22.80248,img:require("../assets/stations/station2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
        {name:"豪岗社区一级消防站",deviceNum:"LGW3_001",num:"0310",lng:113.522484,lat:23.166938,img:require("../assets/stations/station1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        {name:"潭州社区一级消防站",deviceNum:"LGW3_002",num:"0311",lng:113.473975,lat:23.17232,img:require("../assets/stations/station2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
      ],
      stationMarkers:[],
      stationVideo:false,
      stationMsg:"",
      // 微型消防站
      miniStatus:false,
      miniStations:[
        // {name:"二湾社区微型消防站",deviceNum:"LGW4_001",num:"0410",lng:113.443618,lat:22.816563,img:require("../assets/stations/mini1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        // {name:"江滘社区微型消防站",deviceNum:"LGW4_002",num:"0411",lng:113.452601,lat:22.810633,img:require("../assets/stations/mini2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
        {name:"二湾社区微型消防站",deviceNum:"LGW4_001",num:"0410",lng:113.539803,lat:23.173383,img:require("../assets/stations/mini1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        {name:"江滘社区微型消防站",deviceNum:"LGW4_002",num:"0411",lng:113.521765,lat:23.196106,img:require("../assets/stations/mini2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
      ],
      miniMarkers:[],
      //消防控制室
      roomStatus:false,
      rooms:[
        // {name:"灵山社区消防控制室",deviceNum:"LGW5_001",num:"0510",lng:113.379731,lat:22.829754,img:require("../assets/controlRoom/control1.jpg"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        // {name:"镇南社区消防控制室",deviceNum:"LGW5_002",num:"0511",lng:113.432192,lat:22.806769,img:require("../assets/controlRoom/control2.jpg"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
        {name:"灵山社区消防控制室",deviceNum:"LGW5_001",num:"0510",lng:113.466286,lat:23.194645,img:require("../assets/controlRoom/control1.jpg"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        {name:"镇南社区消防控制室",deviceNum:"LGW5_002",num:"0511",lng:113.54239,lat:23.198498,img:require("../assets/controlRoom/control2.jpg"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
      ],
      roomMarkers:[],
      // 数据统计
      // message:[
      //   {unit:"渲乐幼儿园",name:"一键手动报警",time:"09:14:37",type:"火警",result:"已处理",src:require("../assets/fire.png")},
      //   {unit:"工业二路",name:"电话报警",time:"12:31:46",type:"火警",result:"已处理",src:require("../assets/fire2.png")},
      //   {unit:"沙坑工业区",name:"电话报警",time:"12:45:08",type:"山体滑坡",result:"已处理",src:require("../assets/shan.jpg")},
      //   {unit:"废旧金属市场",name:"电话报警",time:"15:57:12",type:"建筑塌方",result:"已处理",src:require("../assets/building.png")},
      //   {unit:"文发百货超市",name:"一键手动报警",time:"20:16:47",type:"电梯伤人",result:"已处理",src:require("../assets/elevator.png")}
      // ],
      alarmMessage:[],
      alarms:[],
      dataShow:false,
      alarmStatus:false,
      alarmMsg:"",
      zIndex:10
    }
  },
  created(){
    this.$Axios.get(this.$url+"/fire/getFireDeviceInfo.do")
                .then((res)=>{
                  // if(window.console){
                  //   console.log(res);
                  // }
                  if(res.data.status){
                      let response=res.data.list;
                      for(let i=0;i<response.length;i++){
                        if(response[i].deviceTypeValue=="firehydrant"){
                          this.hydrants.push(response[i]);
                        }
                        if(response[i].deviceTypeValue=="md_smoke"){
                          this.smokes.push(response[i]);
                        }
                        if(response[i].deviceTypeValue=="weisidun"){
                          this.wsd.push(response[i]);
                        }
                      }
                  }
                }).catch((err)=>{
                  if(window.console){
                    console.log(err);
                  }
                })
    this.$Axios.get(this.$url+"/fire/getFireAlarmInfo.do")
                .then((res)=>{
                  // if(window.console){
                  //   console.log(res);
                  // }
                  if(res.data.status){
                     this.alarmMessage=res.data.list;
                  }
                }).catch((err)=>{
                  if(window.console){
                    console.log(err);
                  }
                })
    setInterval(()=>{
      this.$Axios.get(this.$url+"/fire/getFireAlarmRealTimeInfo.do",{params:{
                    t:Date.parse(new Date())
                  }})
                .then((res)=>{
                  // if(window.console){
                  //   console.log(res);
                  // }
                  if(res.data.status){
                      if(window.console){
                        console.log(res.data);
                      }
                      this.alarms.push(...res.data.list);
                      if(this.alarms.length>4){
                        this.alarms=this.alarms.slice(-4);
                      }
                  }
                }).catch((err)=>{
                  if(window.console){
                    console.log(err);
                  }
                })
    },1000)
  },
  mounted(){
    let map = new BMap.Map("mapshow");
    // let point = new BMap.Point(113.412588,22.809293); //大岗镇坐标
    let point = new BMap.Point(113.508614,23.182885); //萝岗坐标
    map.centerAndZoom(point,15);
    map.enableScrollWheelZoom(true);
    map.setMapStyleV2({styleJson:styleJson});
    this.map=map;
    // this.showDaGang();
  },
  watch:{
    firemenStatus(newValue){
      let _this=this;
      if(newValue){
        for(let i=0;i<this.firemen.length;i++){
          let point = new BMap.Point(this.firemen[i].lng,this.firemen[i].lat);
          let myIcon = new BMap.Icon(require("../assets/fireman2.png"), new BMap.Size(28, 28));
          let marker = new BMap.Marker(point,{icon:myIcon});      
          this.map.addOverlay(marker);
          let position=null;
          let geoc = new BMap.Geocoder();  
          geoc.getLocation(point,(rs)=>{
              let addComp = rs.addressComponents;
              position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
          })
          marker.addEventListener("click",()=>{  
            // let sContent=``;
            // let infoWindow = new BMap.InfoWindow(sContent);  // 创建信息窗口对象  
            // _this.map.openInfoWindow(infoWindow,point); 
            let msg=`
                    <div style="width:30%;height:100%;float:left;background-color:#101213;padding:10px;">
                      <div style="height:30%;width:100%"><img src=${_this.firemen[i].img} height=100% class="img-circle center-block"></div>
                      <div style="height:50%;width:100%;padding:10px 15px;">
                        <div>设备：${_this.firemen[i].deviceNum}</div>
                        <div>姓名：${_this.firemen[i].name}</div>
                        <div>编号：${_this.firemen[i].num}</div>
                        <div>位置：${position}</div>
                      </div>
                      <div style="height:20%;width:100%;padding:10px;">
                        <img src=${require("../assets/vioce1.png")} height=70% style="cursor: pointer;">
                        <img src=${require("../assets/micro.png")} height=70% style="padding-left:15%;cursor: pointer;">
                        <img src=${require("../assets/phone1.png")} height=70% style="padding-left:15%;cursor: pointer;">
                      </div>
                    </div>
                    <div style="width:70%;height:100%;float:left;background-color: #101213;">
                      <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab" style="height:100% !important;width:100% !important;">
                          <param name="mrl" value=${_this.firemen[i].video} />
                          <param name="volume" value="50" />
                          <param name="autoplay" value="true" />
                          <param name="loop" value="false" />
                          <param name="fullscreen" value="true" />
                      </object>
                    </div>`;
            _this.firemenMsg=msg;
            _this.firemenVideo=true;
          });   
          this.firemenMarkers.push(marker);
      }
      }else{
        for(let i=0;i<this.firemenMarkers.length;i++){
          this.map.removeOverlay(this.firemenMarkers[i]);
        }
        this.firemenMarkers=[];
      }
    },
    carStatus(newValue){
      let _this=this;
      if(newValue){
        for(let i=0;i<this.cars.length;i++){
          let point = new BMap.Point(this.cars[i].lng,this.cars[i].lat);
          let myIcon = new BMap.Icon(require("../assets/car2.png"), new BMap.Size(28, 28));
          let marker = new BMap.Marker(point,{icon:myIcon});      
          this.map.addOverlay(marker);
          let position=null;
