    opacity: 0.8;
    border: 2px solid white;
    #tableHead{
      width: 100%;
      height: 30px;
      background-color: #023c68;
      color:rgb(2, 252, 196);
      .cell{
        width: 16.6%;
        height: 100%;
        float: left;
        padding-top:1.5%;
        text-align: center;
      }
    }
    #tableBody{
      width: 100%;
      height:calc(100% - 30px);
      overflow: auto;
      .cells{
        width: 100%;
        height: 40px;
        border-bottom: 1px solid white;
        .cell{
          width: 16.6%;
          height: 100%;
          float: left;
          padding-top:1.5%;
          text-align: center;
        }
      }
    }
  }
  #alarmPicture{
    z-index: 3;
    top:calc(150px + 20%);
    left:calc(15px + 31%);
  }
  .warn{
    // height: 300px;
    width: 400px;
    // background-color: #99d4ff;
    position:absolute;
    right: 18px;
    bottom:18px;
  }
}
.BMap_bubble_title{
    color:black;
    font-size:13px;
    font-weight: bold;
    text-align:left;
}
.BMap_pop div:nth-child(1){
    border-radius:7px 0 0 0;
}
.BMap_pop div:nth-child(3){
    border-radius:0 7px 0 0;background:#ABABAB;;
    /*background: #ABABAB;*/
    width:23px;
    width:0px;
    height:0px;
}
.BMap_pop div:nth-child(3) div{
    border-radius:7px;
}
.BMap_pop div:nth-child(5){
    border-radius:0 0 0 7px;
}
.BMap_pop div:nth-child(5) div{
    border-radius:7px;
}
.BMap_pop div:nth-child(7){
    border-radius:0 0 7px 0 ;
}
.BMap_pop div:nth-child(7){
    border-radius:0 0 7px 0 ;
}
.ivu-icon{
  cursor: pointer;
}
</style>





